  color: white;
}
.fa-instagram {
  background: #125688;
  color: white;
}
.streaming{
  text-align: center;
}
/* gallery */
.grid{
  width: 960px;
  margin: 0 auto;
}

.grid-item {
  height: auto;
  width: 200px;
  margin-bottom: 20px;
}

.grid-item img {
  height: auto;
  width: 100%;
}





.masonry {
  -webkit-column-count: 1;
          column-count: 1;
  grid-column-gap: 0;
  -webkit-column-gap: 0;
          column-gap: 0;
  counter-reset: item-counter;
  width: 960px;
  margin: 0 auto;
}
@media screen and (min-width: 400px) {
  .masonry {
		-webkit-column-count: 2;
		        column-count: 2;
  }
}
@media screen and (min-width: 600px) {
  .masonry {
		-webkit-column-count: 3;
		        column-count: 3;
  }
}
@media screen and (min-width: 980px) {
  .masonry {
		-webkit-column-count: 4;
		        column-count: 4;
		/* width: 100%; */
  }
}
@media screen and (min-width: 1100px) {
  .masonry {
		-webkit-column-count: 5;
		        column-count: 5;
  }
}

.item {
  box-sizing: border-box;
  page-break-inside: avoid;
  -webkit-column-break-inside: avoid;
          break-inside: avoid;
  page-break-inside: avoid;
  padding: 10px;
}

.item img {
  height: auto;
  width: 100%;
}






/* this is for magic-8-ball */
#outputDiv {
	display: inline-block;
	position: relative;
}

.caption {
	color: #fff;
	position: absolute;
	z-index: 1;
	left: 0;
	right: 0;
	bottom: 50px;
	font-weight: bold;
	text-align: center;
	text-shadow: 0 0 4px #000, 0 0 4px #000;
}

.ee {
	display: block;
	position: relative;
	-webkit-animation: ee 1s infinite;
	        animation: ee 1s infinite;
}

@-webkit-keyframes ee {
  0%   {color: red;}
  25%  {color: yellow;}
  50%  {color: blue;}
  75%  {color: cyan;}
  100% {color: green;}

	0%   {text-transform:uppercase;}
  33%  {text-transform:capitalize;}
  66%  {text-transform:lowercase;}
  100% {text-transform:none;}

	0% {
		bottom: -5px;
	}

	50% {
		bottom: 5px;
	}

	100% {
		bottom: -5px;
	}
}

@keyframes ee {
  0%   {color: red;}
  25%  {color: yellow;}
  50%  {color: blue;}
  75%  {color: cyan;}
  100% {color: green;}

	0%   {text-transform:uppercase;}
  33%  {text-transform:capitalize;}
  66%  {text-transform:lowercase;}
  100% {text-transform:none;}

	0% {
		bottom: -5px;
	}

	50% {
		bottom: 5px;
	}

	100% {
		bottom: -5px;
	}
}

.videoWrapper {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
}
.videoWrapper iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

